* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "sans-serif";
  background-color: #1b7e7e;
  color: #ffff;
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: azure;
}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #f1f7f4;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 8px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #09020c 0%,
    #000000 48%,
    #05010f 100%
  );
  color: #f7ebeb;
}
